* {
  background-color: azure;
  text-align: center;
}
.App {
  font-family: sans-serif;
  text-align: center;
}
.div {
  text-align: center;
  display: flex;
  flex-flow: column;
  align-items: center;
}
form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  background-color: gold;
  width: 500px;
  padding: 40px;
  border-radius: 17px;
}

input {
  border-radius: 7px;
}

button {
  border-radius: 7px;
}

.div2 {
  display: flex;
  flex-flow: row wrap;
  border: 2px solid black;
  margin: 25px 30px;
  min-height: 400px;
  background-color: darkgrey;
}
.paragraph {
  height: 35px;
  margin: 35px;
  padding: 15px;
  border: 1px solid black;
  border-radius: 7px;
  background-color: darkkhaki;
}

.goback {
  width: 170px;
  font-size: 30px;
  background-color: darkgray;
}
